main {
    margin: 85px;  overflow:hidden;
}

.container {
    display: grid;
    grid-auto-columns: 1fr;
    grid-template-columns: repeat(110 1fr);
    grid-template-rows: repeat(50 1fr);
    gap: 0px 0px;
    align-content: center;
    justify-items: stretch;
    width: 1080px;
    height: 590px;
    justify-items: stretch;
    align-items:stretch;
  

}

