.banner {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.bannerBig {
    font-family: Spartan;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 25px;

    letter-spacing: 0.06em;
    text-transform: uppercase;
   
    color: #121212;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    justify-content: center;

}

.bannerBig2 {
    font-family: Spartan;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    justify-content: center;
}


